/* Requires: normalize.css */


/* Global Reset & Standards ---------------------- */

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

html {
    font-size: 62.5%
}

body {
    font-family: DroidSansRegular, "Helvetica Neue", "Helvetica", Helvetica, Arial, sans-serif;
    font-size: 13px;
    line-height: 20px;
    color: #666;
    position: relative;
    -webkit-font-smoothing: antialiased;
}


/* Links ---------------------- */

a {
    color: rgb(26, 237, 26);
    text-decoration: none;
    line-height: inherit;
}

a:hover {
    color: #2795b6
}

a:focus {
    color: lime;
    outline: none;
}

p a,
p a:visited {
    line-height: inherit
}

img {
    border: 0px;
}

#googlemap img,
object,
embed {
    max-width: none
}

#map_canvas embed {
    max-width: none
}

#map_canvas img {
    max-width: none
}

#map_canvas object {
    max-width: none
}


/* Reset for strange margins by default on <figure> elements */

figure {
    margin: 0
}


/* Base Type Styles Using Modular Scale ---------------------- */

body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
form,
p,
blockquote,
th,
td {
    margin: 0;
    padding: 0;
    font-size: 13px;
    direction: ltr;
}

p {
    font-size: 13px;
    line-height: 1.6;
    margin-bottom: 15px;
}

p.lead {
    font-size: 17.5px;
    line-height: 1.6;
    margin-bottom: 17px;
}

::-webkit-input-placeholder {
    font-size: 12px
}

aside p {
    font-size: 13px;
    line-height: 1.35;
    font-style: italic;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Helvetica Neue", "Helvetica", Helvetica, Arial, sans-serif;
    /* color: #222222; */
    text-rendering: optimizeLegibility;
    line-height: 1.1;
    margin-bottom: 14px;
    /* margin-top: 14px; */
}

h1 small,
h2 small,
h3 small,
h4 small,
h5 small,
h6 small {
    font-size: 60%;
    color: #6f6f6f;
    line-height: 0;
}

h1 {
    font-size: 40px
}

h2 {
    font-size: 37px
}

h3 {
    font-size: 27px
}

h4 {
    font-size: 23px
}

h5 {
    font-size: 17px
}

h6 {
    font-size: 14px
}

hr {
    border: solid #ddd;
    border-width: 1px 0 0;
    clear: both;
    margin: 22px 0 21px;
    height: 0;
}

.subheader {
    line-height: 1.3;
    color: #6f6f6f;
    font-weight: 300;
    margin-bottom: 17px;
}

em,
i {
    font-style: italic;
    line-height: inherit;
}

strong,
b {
    font-weight: bold;
    line-height: inherit;
}

small {
    font-size: 60%;
    line-height: inherit;
}

code {
    font-weight: bold;
    background: #ffff99;
}


/* Lists ---------------------- */

ul,
ol {
    font-size: 14px;
    line-height: 1.6;
    margin-bottom: 17px;
    list-style-position: inside;
}

ul li ul,
ul li ol {
    margin-left: 20px;
    margin-bottom: 0;
}

ul.square,
ul.circle,
ul.disc {
    margin-left: 17px
}

ul.square {
    list-style-type: square
}

ul.square li ul {
    list-style: inherit
}

ul.circle {
    list-style-type: circle
}

ul.circle li ul {
    list-style: inherit
}

ul.disc {
    list-style-type: disc
}

ul.disc li ul {
    list-style: inherit
}

ul.no-bullet {
    list-style: none
}

ul.large li {
    line-height: 21px
}

ol li ul,
ol li ol {
    margin-left: 20px;
    margin-bottom: 0;
}


/* Blockquotes ---------------------- */

blockquote,
blockquote p {
    /* color: #6f6f6f; */
}

blockquote {
    margin: 0 0 17px;
    padding: 9px 20px 0 19px;
}

blockquote cite {
    display: block;
    font-size: 13px;
    color: #555555;
}

blockquote cite:before {
    content: "\2014 \0020"
}

blockquote cite a,
blockquote cite a:visited {
    color: #555555
}

abbr,
acronym {
    text-transform: uppercase;
    font-size: 90%;
    color: #222222;
    border-bottom: 1px solid #ddd;
    cursor: help;
}

abbr {
    text-transform: none
}


/* Print styles.  Inlined to avoid required HTTP connection: www.phpied.com/delay-loading-your-print-css/ Credit to Paul Irish and HTML5 Boilerplate (html5boilerplate.com)
*/

.print-only {
    display: none !important
}

@media print {
    * {
        background: transparent !important;
        color: black !important;
        box-shadow: none !important;
        text-shadow: none !important;
        filter: none !important;
        -ms-filter: none !important;
    }
    /* Black prints faster: h5bp.com/s */
    a,
    a:visited {
        text-decoration: underline
    }
    a[href]:after {
        content: " ("attr(href) ")"
    }
    abbr[title]:after {
        content: " ("attr(title) ")"
    }
    .ir a:after,
    a[href^="javascript:"]:after,
    a[href^="#"]:after {
        content: ""
    }
    /* Don't show links for images, or javascript/internal links */
    pre,
    blockquote {
        border: 1px solid #999;
        page-break-inside: avoid;
    }
    thead {
        display: table-header-group
    }
    /* h5bp.com/t */
    tr,
    img {
        page-break-inside: avoid
    }
    img {
        max-width: 100% !important
    }
}


