    content: "\2013";
    position: absolute;
    right: 10px;
    color: #8ed3e7;
}

form.custom div.custom.dropdown ul li.selected:hover {
    background: #cdebf5;
    cursor: default;
    color: #000;
}

form.custom div.custom.dropdown ul li.selected:hover:after {
    color: #000
}

form.custom div.custom.dropdown ul.show {
    display: block
}

form.custom .custom.disabled {
    background-color: #ddd
}


