}

ul.flyout li.active a,
.nav-bar li ul li.active a {
    background: #fff;
    border: none;
    color: #fff;
    height: auto;
    margin: 0;
    position: static;
    top: 0;
    -webkit-box-shadow: 0 0 0;
    -moz-box-shadow: 0 0 0;
    box-shadow: 0 0 0;
}


/* Correct timer in IE */

.lt-ie9 .timer {
    display: none !important
}

.lt-ie9 div.caption {
    background: transparent;
