/* Tabs ---------------------- */

dl.tabs {
    border-bottom: solid 1px #e6e6e6;
    display: block;
    height: 40px;
    padding: 0;
    margin-bottom: 20px;
}

dl.tabs.contained {
    margin-bottom: 0
}

dl.tabs dt {
    color: #b3b3b3;
    cursor: default;
    display: block;
    float: left;
    font-size: 12px;
    height: 40px;
    line-height: 40px;
    padding: 0;
    padding-right: 9px;
    padding-left: 20px;
    width: auto;
    text-transform: uppercase;
}

dl.tabs dt:first-child {
    padding: 0;
    padding-right: 9px;
}

dl.tabs dd {
    display: block;
    float: left;
    padding: 0;
    margin: 0;
}

dl.tabs dd a {
    color: #6f6f6f;
    display: block;
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    padding: 0px 23.8px;
}

dl.tabs dd a:focus {
    font-weight: bold;
    color: #2ba6cb;
}

dl.tabs dd.active {
    margin-top: -3px
}

dl.tabs dd.active a {
    cursor: default;
    color: #3c3c3c;
    font-weight: bold;
}

dl.tabs dd:first-child {
    margin-left: 0
}

dl.tabs.vertical {
    height: auto;
    border-bottom: 1px solid #e6e6e6;
}

dl.tabs.vertical dt,
dl.tabs.vertical dd {
    float: none;
    height: auto;
}

dl.tabs.vertical dd {
    border-left: 3px solid #cccccc
}

dl.tabs.vertical dd a {
    background: #f2f2f2;
    border: none;
    border: 1px solid #e6e6e6;
    border-width: 1px 1px 0 0;
    color: #555;
    display: block;
    font-size: 14px;
    height: auto;
