}

form.custom div.custom.dropdown a.selector:after {
    content: "";
    display: block;
    content: "";
    display: block;
    width: 0;
    height: 0;
    border: solid 5px;
    border-color: #aaaaaa transparent transparent transparent;
    position: absolute;
