/* Alerts ---------------------- */

div.alert-box {
    display: block;
    padding: 6px 7px 7px;
    font-weight: bold;
    font-size: 14px;
    color: white;
    background-color: #2ba6cb;
    border: 1px solid rgba(0, 0, 0, 0.1);
    margin-bottom: 12px;
    border-radius: 3px;
    position: relative;
}

div.alert-box.default {
    background-color: #EDC60F;
    color: #fff;
}

div.alert-box.success {
    background-color: #5da423;
    color: #fff;
    text-shadow: 0 -1px rgba(0, 0, 0, 0.3);
}

div.alert-box.alert {
    background-color: #c60f13;
    color: #fff;
    text-shadow: 0 -1px rgba(0, 0, 0, 0.3);
}

div.alert-box.secondary {
    background-color: #e9e9e9;
    color: #505050;
    text-shadow: 0 1px rgba(255, 255, 255, 0.3);
}

div.alert-box a.close {
    color: #333;
    position: absolute;
    right: 4px;
    top: -1px;
    font-size: 17px;
    opacity: 0.2;
    padding: 4px;
}

div.alert-box a.close:hover,
div.alert-box a.close:focus {
    opacity: 0.4
}


/* Labels ---------------------- */

.label {
    padding: 1px 4px 2px;
    font-size: 12px;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    line-height: 1;
    white-space: nowrap;
    display: inline;
    position: relative;
    bottom: 1px;
    color: #fff;
    background: #2ba6cb;
}

.label.radius {
    border-radius: 3px;
}

.label.round {
    padding: 1px 7px 2px;
    border-radius: 1000px;
}

.label.alert {
    background-color: #c60f13
}

.label.success {
    background-color: #5da423
}

.label.secondary {
    background-color: #e9e9e9;
    color: #505050;
}


/* Tooltips ---------------------- */

.has-tip {
    cursor: default;
    color: #333333;
}

.has-tip:hover {
    color: #2ba6cb
}

.has-tip.tip-left,
.has-tip.tip-right {
    float: none !important
}

.tooltip {
    display: none;
    background: black;
    background: rgba(0, 0, 0, 0.85);
    position: absolute;
    color: white;
    font-weight: bold;
    font-size: 12px;
    font-size: 1.2rem;
    padding: 5px;
    z-index: 999;
    border-radius: 4px;
    line-height: normal;
}

.tooltip>.nub {
    display: block;
    width: 0;
    height: 0;
    border: solid 5px;
    border-color: transparent transparent black transparent;
    border-color: transparent transparent rgba(0, 0, 0, 0.85) transparent;
    position: absolute;
    top: -10px;
    left: 10px;
}

.tooltip.tip-override>.nub {
    border-color: transparent transparent black transparent !important;
    border-color: transparent transparent rgba(0, 0, 0, 0.85) transparent !important;
    top: -10px !important;
}

.tooltip.tip-top>.nub {
    border-color: black transparent transparent transparent;
    border-color: rgba(0, 0, 0, 0.85) transparent transparent transparent;
    top: auto;
    bottom: -10px;
}

.tooltip.tip-left,
.tooltip.tip-right {
    float: none !important
}

.tooltip.tip-left>.nub {
    border-color: transparent transparent transparent black;
    border-color: transparent transparent transparent rgba(0, 0, 0, 0.85);
    right: -10px;
    left: auto;
}

.tooltip.tip-right>.nub {
    border-color: transparent black transparent transparent;
    border-color: transparent rgba(0, 0, 0, 0.85) transparent transparent;
    right: auto;
    left: -10px;
}

.tooltip.noradius {
    border-radius: 0;
}

.tooltip.opened {
    color: #2ba6cb !important;
    border-bottom: dotted 1px #196177 !important;
}

.tap-to-close {
    display: block;
    font-size: 10px;
    font-size: 1rem;
    color: #888888;
    font-weight: normal;
}


/* Panels ---------------------- */

.panel {
    background: #f9f9f9;
    margin: 0 0 22px 0;
    padding: 20px;
}

.panel> :first-child {
    margin-top: 0
}

.panel> :last-child {
    margin-bottom: 0
}

.panel.callout {
    background: #2ba6cb;
    color: #fff;
    border-color: #2284a1;
    box-shadow: inset 0px 1px 0px rgba(255, 255, 255, 0.5);
}

.panel.callout a {
    color: #fff
}

.panel.callout .button {
    background: white;
    border: none;
    color: #2ba6cb;
    text-shadow: none;
}

.panel.callout .button:hover {
    background: rgba(255, 255, 255, 0.8)
}

.panel.radius {
    border-radius: 3px;
}


/* Accordion ---------------------- */

ul.accordion {
    margin: 0 0 22px 0;
    border-bottom: 1px solid #e9e9e9;
}

ul.accordion>li {
    list-style: none;
    margin: 0;
    padding: 0;
    border-top: 1px solid #e9e9e9;
}

ul.accordion>li .title {
    cursor: pointer;
    background: #f6f6f6;
    padding: 15px;
    margin: 0;
    position: relative;
    border-left: 1px solid #e9e9e9;
    border-right: 1px solid #e9e9e9;
    transition: 0.15s background linear;
}

ul.accordion>li .title h1,
ul.accordion>li .title h2,
ul.accordion>li .title h3,
ul.accordion>li .title h4,
ul.accordion>li .title h5 {
    margin: 0
}

ul.accordion>li .title:after {
    content: "";
    display: block;
    width: 0;
    height: 0;
    border: solid 6px;
    border-color: transparent #9d9d9d transparent transparent;
    position: absolute;
    right: 15px;
    top: 21px;
}

ul.accordion>li .content {
    display: none;
    padding: 15px;
}

ul.accordion>li.active {
    border-top: 3px solid #2ba6cb
}

ul.accordion>li.active .title {
    background: white;
    padding-top: 13px;
}

ul.accordion>li.active .title:after {
    content: "";
    display: block;
    width: 0;
    height: 0;
    border: solid 6px;
    border-color: #9d9d9d transparent transparent transparent;
}

ul.accordion>li.active .content {
    background: white;
    display: block;
    border-left: 1px solid #e9e9e9;
    border-right: 1px solid #e9e9e9;
}


/* Side Nav ---------------------- */

ul.side-nav {
    display: block;
    list-style: none;
    margin: 0;
    padding: 17px 0;
}

ul.side-nav li {
    display: block;
    list-style: none;
    margin: 0 0 7px 0;
}

ul.side-nav li a {
    display: block
}

ul.side-nav li.active a {
    color: #4d4d4d;
    font-weight: bold;
}

ul.side-nav li.divider {
    border-top: 1px solid #e6e6e6;
    height: 0;
    padding: 0;
}


/* Sub Navs http://www.zurb.com/article/292/how-to-create-simple-and-effective-sub-na ---------------------- */

dl.sub-nav {
    display: block;
    width: auto;
    overflow: hidden;
    margin: -4px 0 18px;
    margin-right: 0;
    margin-left: -9px;
    padding-top: 4px;
}

dl.sub-nav dt,
dl.sub-nav dd {
    float: left;
    display: inline;
    margin-left: 9px;
    margin-bottom: 10px;
}

dl.sub-nav dt {
    color: #999;
    font-weight: normal;
}

dl.sub-nav dd a {
    text-decoration: none;
    border-radius: 1000px;
}

dl.sub-nav dd.active a {
    font-weight: bold;
    background: #2ba6cb;
    color: #fff;
    padding: 3px 9px;
    cursor: default;
}


/* Pagination ---------------------- */

ul.pagination {
    display: block;
    height: 24px;
    margin-left: -5px;
}

ul.pagination li {
    float: left;
    display: block;
    height: 24px;
    color: #999;
    font-size: 14px;
    margin-left: 5px;
}

ul.pagination li a {
    display: block;
    padding: 3px 7px 0px;
    color: #555;
}

ul.pagination li:hover a,
ul.pagination li a:focus {
    background: #e6e6e6
}

ul.pagination li.unavailable a {
    cursor: default;
    color: #999;
}

ul.pagination li.unavailable:hover a,
ul.pagination li.unavailable a:focus {
    background: transparent
}

ul.pagination li.current a {
    background: #2ba6cb;
    color: white;
    font-weight: bold;
    cursor: default;
}

ul.pagination li.current a:hover {
    background: #333
}


/* Breadcrums ---------------------- */

ul.breadcrumbs {
    display: block;
    background: #f6f6f6;
    padding: 6px 10px 7px;
    border: 1px solid #e9e9e9;
    border-radius: 2px;
    overflow: hidden;
}

ul.breadcrumbs li {
    margin: 0;
    padding: 0 12px 0 0;
    float: left;
    list-style: none;
}

ul.breadcrumbs li a,
ul.breadcrumbs li span {
    text-transform: uppercase;
    font-size: 11px;
    font-size: 1.1rem;
    padding-left: 12px;
}

ul.breadcrumbs li:first-child a,
ul.breadcrumbs li:first-child span {
    padding-left: 0
}

ul.breadcrumbs li:before {
    content: "/";
    color: #aaa;
}

ul.breadcrumbs li:first-child:before {
    content: " "
}

ul.breadcrumbs li.current a {
    cursor: default;
    color: #333;
}

ul.breadcrumbs li:hover a,
ul.breadcrumbs li a:focus {
    text-decoration: underline
}

ul.breadcrumbs li.current:hover a,
ul.breadcrumbs li.current a:focus {
    text-decoration: none
}

ul.breadcrumbs li.unavailable a {
    color: #999
}

ul.breadcrumbs li.unavailable:hover a,
ul.breadcrumbs li.unavailable a:focus {
    text-decoration: none;
    color: #999;
    cursor: default;
}


/* Link List */

ul.link-list {
    margin: 0 0 17px -22px;
    padding: 0;
    list-style: none;
    overflow: hidden;
}

ul.link-list li {
    list-style: none;
    float: left;
    margin-left: 22px;
    display: block;
}

ul.link-list li a {
    display: block
}


/* Keytroke Characters ---------------------- */

.keystroke,
kbd {
    font-family: "Consolas", "Menlo", "Courier", monospace;
    font-size: 13px;
    padding: 2px 4px 0px;
    margin: 0;
    background: #ededed;
    border: solid 1px #dbdbdb;
    border-radius: 3px;
}


/* Image Thumbnails ---------------------- */

.th {
    display: block
}

.th img {
    display: block;
    border: solid 4px #fff;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.2);
    border-radius: 3px;
    transition-property: box-shadow;
    transition-duration: 300ms;
}

.th:hover img {
    box-shadow: 0 0 6px 1px rgba(43, 166, 203, 0.5);
}


/* Video - Mad props to http://www.alistapart.com/articles/creating-intrinsic-ratios-for-video/ ---------------------- */

.flex-video {
    position: relative;
    padding-top: 25px;
    padding-bottom: 67.5%;
    height: 0;
    margin-bottom: 16px;
    overflow: hidden;
}

.flex-video.widescreen {
    padding-bottom: 57.25%
}

.flex-video.vimeo {
    padding-top: 0
}

.flex-video iframe,
.flex-video object,
.flex-video embed,
.flex-video video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}


/* Foundation Top Bar
 Requires: -jquery.foundation.topbar.js
 Notes: - <nav class="top-bar"> can have .fixed added to make it stick to the top of viewport. Can add .contain-to-grid to top-bar to make it full width but content on grid. - <li class="name"> creates a branding area that can be linked to homepage - Can include text, image or both inside this <li>. - <li class="divider"> creates a divider between nav items, add margins for space - <li class="search"> Needs to hold a <form> which can use .row.collapse and pre/postfix buttons or default type="search"
*/


/* Element that controls breakpoint, no need to change this ever */

.top-bar-js-breakpoint {
    width: 960px !important;
    visibility: hidden;
}


/* Wrapped around .top-bar to contain to grid width */

.contain-to-grid {
    width: 100%
}


/* Wrapped around .top-bar to make it fixed at the top */

.fixed {
    width: 100%;
    left: 0;
    position: fixed;
    top: 0;
    z-index: 99;
}


/* <nav> */

.top-bar {
    nowhitespace: afterproperty;
    height: 45px;
    line-height: 45px;
    margin: 0 0 30px;
    padding: 0;
    width: 100%;
}


/* Firefox Fixes */

@-moz-document url-prefix() {
    .top-bar ul li .button.small {
        padding-bottom: 6px
    }
    .top-bar ul li.search form input[type=search] {
        font-size: 14px;
        height: 22px;
        padding: 3px;
    }
}


/* IE8 Fixes */

.lt-ie9 .top-bar ul li a {
    color: white;
    display: block;
    font-weight: bold;
    font-size: 13px;
    height: 45px;
    line-height: 45px;
    padding: 0 15px;
}

.lt-ie9 .top-bar ul li a.button {
    height: auto;
    line-height: 30px;
    margin-top: 7px;
}

.lt-ie9 .top-bar ul li a:hover {
    color: #cccccc
}

.lt-ie9 .top-bar ul li a img {
    margin-top: -5px;
    vertical-align: middle;
}

.lt-ie9 .top-bar ul li a.active {
    background: #151515;
    color: #d9d9d9;
}

.lt-ie9 .top-bar ul li.has-dropdown {
    padding-right: 33.75px
}

.lt-ie9 .top-bar ul li.has-dropdown>ul li {
    padding-right: 0
}


/* Tables ---------------------- */

table {
    /* background: #fff; */
    border-radius: 3px;
    margin: 0 0 18px;
    /* border: 1px solid #ddd; */
}

table thead,
table tfoot {
    background: #f5f5f5
}

table thead tr th,
table tfoot tr th,
table tbody tr td,
table tr td,
table tfoot tr td {
    font-size: 13px;
    line-height: 18px;
    text-align: left;
}

table thead tr th,
table tfoot tr td {
    padding: 8px 10px 9px;
    font-size: 16px;
    font-weight: bold;
    color: #222;
}

table thead tr th:first-child,
table tfoot tr td:first-child {
    border-left: none
}

table thead tr th:last-child,
table tfoot tr td:last-child {
    border-right: none
}

table tbody tr.even,
table tbody tr.alt {
    background: #f9f9f9
}

table tbody tr:nth-child(even) {
    background: #f9f9f9
}

table tbody tr td {
    color: #333;
    padding: 9px 10px;
    vertical-align: top;
    border: none;
}


/* Microformats ---------------------- */

ul.vcard {
    display: inline-block;
    margin: 0 0 12px 0;
    border: 1px solid #ddd;
    padding: 10px;
}

ul.vcard li {
    margin: 0;
    display: block;
}

ul.vcard li.fn {
    font-weight: bold;
    font-size: 15px;
    font-size: 1.5rem;
}

p.vevent span.summary {
    font-weight: bold
}

p.vevent abbr {
    cursor: default;
    text-decoration: none;
    font-weight: bold;
    border: none;
    padding: 0 1px;
}


/* Progress Bar ---------------------- */

div.progress {
    padding: 2px;
    nowhitespace: afterproperty;
    nowhitespace: afterproperty;
    margin-bottom: 10px;
    border: 1px solid #cccccc;
    height: 25px;
    /* meter */
    /* border radii */
}

div.progress .meter {
    background: #2ba6cb;
    height: 100%;
    display: block;
    width: 50%;
}

div.progress.secondary .meter {
    background: #e9e9e9
}

div.progress.success .meter {
    background: #5da423
}

div.progress.alert .meter {
    background: #c60f13
}

div.progress.radius {
    border-radius: 3px;
}

div.progress.radius .meter {
    border-radius: 2px;
}

div.progress.round {
    border-radius: 1000px;
}

div.progress.round .meter {
    border-radius: 1000px;
}


/* MENU SUPERFISH */

#menu-icon {
    display: none;
    /* hide menu icon initially */
}

.sf-menu,
.sf-menu li {
    margin: 0;
    padding: 0;
    float: right;
}

.sf-menu li {
    list-style: none;
    float: left;
}

.sf-menu a {
    padding: 4px 15px;
    display: block;
    /* color: #000; */
}

.sf-menu a:hover {}

.sf-menu ul {
    padding: 2px;
    position: absolute;
    display: none;
    /* hide dropdown */
    width: 200px;
}

.sf-menu ul li {
    float: none;
    margin: 0px;
    padding: 0px;
}

.sf-menu li:hover>ul {
    display: block;
    /* show dropdown on hover */
}

ul.sf-menu li li:hover ul,
ul.sf-menu li li.sfHover ul {
    left: 200px;
    /* match ul width */
    top: 0;
}

.sf-menu,
.sf-menu * {
    margin: 0;
    padding: 0;
    list-style: none;
}

.sf-menu,
.sf-menu * {
    margin: 0;
    padding: 0;
    list-style: none;
}

.sf-menu {
    line-height: 1.0
}

.sf-menu ul {
    position: absolute;
    top: -999em;
    width: 10em;
}

.sf-menu ul li {
    width: 100%
}

.sf-menu li:hover {
    visibility: inherit
}

.sf-menu li {
    float: left;
    position: relative;
    margin-right: 7px;
}

.sf-menu a {
    display: block;
    position: relative;
}

.sf-menu li:hover ul,
.sf-menu li.sfHover ul {
    left: 0%;
    padding-top: 0px;
    top: 40px;
    /* match top ul list item height */
    z-index: 99;
}

ul.sf-menu li:hover li ul,
ul.sf-menu li.sfHover li ul {
    top: -999em
}

ul.sf-menu li li:hover ul,
ul.sf-menu li li.sfHover ul {
    left: 10em;
    /* match ul width */
