.ca-menu {
    padding: 0;
}

.ca-menu li {
    width: 220px;
    height: 250px;
    overflow: hidden;
    position: relative;
    float: left;
    margin-right: 4px;
    transition: all 300ms linear;
    margin-right: 22px;
    margin-bottom: 10px;
}

.ca-menu li:last-child {
    margin-right: 0px;
}

.ca-menu li a {
    text-align: left;
    width: 100%;
    height: 100%;
    display: block;
    color: #333;
    position: relative;
}

.ca-icon {
    font-family: 'WebSymbolsRegular', cursive;
    font-size: 60px;
    color: #333;
    text-shadow: 0px 0px 1px #333;
    line-height: 120px;
    position: absolute;
    width: 100%;
    height: 50%;
    left: 0px;
    top: 0px;
    text-align: center;
    transition: all 400ms linear;
}

.ca-content {
    position: absolute;
    left: 0px;
    width: 100%;
    height: 50%;
    top: 50%;
}

.ca-main {
    font-size: 20px;
    opacity: 0.8;
    text-align: center;
    transition: all 200ms linear;
}

.ca-sub {
    text-align: center;
    font-size: 14px;
    color: #666;
    line-height: 40px;
    opacity: 0.8;
    transition: all 200ms linear;
    font-family: DroidSansRegular;
}

.ca-menu li:hover {
    background-color: #000;
}

.ca-menu li:hover .ca-icon {
    color: #fff;
    font-size: 60px;
}

.ca-menu li:hover .ca-main {
    -webkit-animation: moveFromLeftRotate 300ms ease;
    -moz-animation: moveFromLeftRotate 300ms ease;
    -ms-animation: moveFromLeftRotate 300ms ease;
}

.ca-menu li:hover .ca-sub {
    color: #fff;
    -webkit-animation: moveFromBottom 500ms ease;
    -moz-animation: moveFromBottom 500ms ease;
    -ms-animation: moveFromBottom 500ms ease;
}


/*******************************************************************************************************************
*     Top Slider                                                                                             
*******************************************************************************************************************/

.ei-slider {
    position: relative;
    width: 100%;
    max-width: 1920px;
    height: 500px;
    margin: 0 auto;
}

.ei-slider-loading {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 999;
    background: rgba(0, 0, 0, 0.9);
    color: #fff;
    text-align: center;
    line-height: 400px;
}

.ei-slider-large {
    height: 100%;
    width: 100%;
    position: relative;
    overflow: hidden;
}

.ei-slider-large li {
    position: absolute;
    top: 0px;
    left: 0px;
    overflow: hidden;
    height: 100%;
    width: 100%;
}

.ei-slider-large li img {
    width: 100%;
    opacity: 1;
}

.ei-title {
    position: absolute;
    top: 30%;
}

.ei-title h2,
.ei-title h3 {
    text-align: center;
}

.ei-title h2 {
    font-size: 40px;
    line-height: 50px;
    color: #fff;
    padding: 0 10px;
}

.ei-title h3 {
    font-size: 50px;
    line-height: 70px;
    text-transform: uppercase;
    color: #fff;
    background: #333;
    padding: 0px 40px;
}

.ei-slider-thumbs {
    height: 13px;
    margin: 0 auto;
    position: relative;
}

.ei-slider-thumbs li {
    position: relative;
    float: left;
    height: 100%;
}

.ei-slider-thumbs li.ei-slider-element {
    top: 0px;
    left: 0px;
    position: absolute;
    height: 100%;
    z-index: 10;
    text-indent: -9000px;
    background: #000;
    background: rgba(0, 0, 0, 0.9);
}

.ei-slider-thumbs li a {
    display: block;
    text-indent: -9000px;
    background: #333;
    width: 100%;
    height: 100%;
    cursor: pointer;
    border-right: 1px solid #555;
    transition: background 0.2s ease;
}

.ei-slider-thumbs li a:hover {
    background-color: #f0f0f0;
}

.ei-slider-thumbs li img {
    position: absolute;
    bottom: 50px;
    opacity: 0;
    z-index: 999;
    max-width: 100%;
    transition: all 0.4s ease;
    -webkit-box-reflect: below 0px -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(50%, transparent), to(rgba(255, 255, 255, 0.3)));
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
}

.ei-slider-thumbs li:hover img {
    opacity: 1;
    bottom: 13px;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}

@media screen and (max-width: 830px) {
    .ei-title {
        position: absolute;
        right: 0px;
        margin-right: 0px;
        width: 100%;
        text-align: center;
        top: auto;
        bottom: 0;
        background: rgba(255, 255, 255, 0.9);
        padding: 0;
    }
    .ei-title h2,
    .ei-title h3 {
        text-align: center;
    }
    .ei-title h2 {
        font-size: 20px;
        line-height: 24px;
    }
    .ei-title h3 {
        font-size: 30px;
        line-height: 40px;
        margin-top: 0px;
        margin-bottom: 1px;
    }
}

